import React from "react";
import gql from "graphql-tag";
import { ApolloClient } from "apollo-client";
import { TYPING_INTERVAL, MESSAGE_TYPE } from "../constants";
import {
  IOnEventResult,
  ON_EVENT,
  IOnEventVariables,
  IOnEventMessageReceived,
  IOnEventConversationTyping
} from "../api/subscriptions/onEvent";
import {
  MESSAGES,
  IMessagesData,
  IMessagesVariables
} from "../api/queries/MessagesQuery";
import { play } from "../utils/audio";
import i18n from "../i18n";
import { IPersonFragment } from "../api/fragments/person";
import { ConversationFragment } from "../api/fragments/conversation";
import { prependTitleUntilFocused } from "../utils/document";
import { MessageFragment } from "../api/fragments/message";
import { CustomerContext } from "./CustomerManager";

interface IProps {
  client: ApolloClient<{}>;
}

export type SubscriptionManagerAction = {
  type:
    | "MESSAGE_RECEIVED"
    | "ADD_CONVERSATION_TYPING"
    | "REMOVE_CONVERSATION_TYPING";
  payload: IOnEventMessageReceived | IOnEventConversationTyping;
};

export type SubscriptionManagerDispatch = (
  action: SubscriptionManagerAction
) => void;

export const SubscriptionManagerContext = React.createContext(
  (action: SubscriptionManagerAction) => {}
);

export default class SubscriptionManager extends React.Component<IProps> {
  subscription?: ZenObservable.Subscription;
  mounted: boolean = false;
  timeoutTyping: { [key: string]: NodeJS.Timeout } = {};

  static contextType = CustomerContext;
  context!: React.ContextType<typeof CustomerContext>;

  componentDidMount() {
    const { client } = this.props;

    this.mounted = true;

    this.subscription = client
      .subscribe<IOnEventResult, IOnEventVariables>({
        query: ON_EVENT
      })
      .subscribe(
        ({ data }) => {
          switch (data.onEvent.name) {
            case "messageReceived":
              this.dispatch({
                type: "MESSAGE_RECEIVED",
                payload: data.onEvent.data
              });
              break;
            case "conversationTyping":
              this.dispatch({
                type: "ADD_CONVERSATION_TYPING",
                payload: data.onEvent.data
              });
              break;
          }
        },
        error => {
          console.error("Error on subscription", error);
        }
      );
  }

  componentWillUnmount() {
    if (!!this.subscription) {
      this.subscription.unsubscribe();
    }
    this.mounted = false;
  }

  dispatch = ({ type, payload }: SubscriptionManagerAction) => {
    const { client } = this.props;

    if (!this.mounted) {
      // setTimeout may calls dispatch after component has been already unmounted
      return;
    }

    console.info(type, payload);

    switch (type) {
      case "MESSAGE_RECEIVED":
        const { message } = payload as IOnEventMessageReceived;
        if (message.type !== MESSAGE_TYPE.MESSAGE) return;

        // append message to messages
        try {
          const data = client.readQuery<IMessagesData, IMessagesVariables>({
            query: MESSAGES,
            variables: { conversationId: message.conversationId }
          });

          if (!data) {
            return;
          }

          const { messages } = data;

          const existingMessage = messages.items.find(m => m.id === message.id);

          if (existingMessage === undefined) {
            client.writeQuery<IMessagesData, IMessagesVariables>({
              query: MESSAGES,
              variables: { conversationId: message.conversationId },
              data: {
                ...data,
                messages: {
                  ...messages,
                  total: messages.total + 1,
                  items: [...messages.items, message].sort((a, b) => {
                    return (
                      Number(new Date(b.createdAt)) -
                      Number(new Date(a.createdAt))
                    );
                  })
                }
              }
            });
          }
        } catch (error) {
          // ignore for now
        }

        // update conversation last message
        try {
          client.writeFragment({
            id: `Conversation:${message.conversationId}`,
            fragment: gql`
              fragment lastMessageFragment on Conversation {
                lastMessage {
                  ...MessageFragment
                }
              }
              ${MessageFragment}
            `,
            fragmentName: "lastMessageFragment",
            data: {
              __typename: "Conversation",
              lastMessage: {
                ...message
              }
            }
          });
        } catch (error) {
          console.log(error);
          // ignore for now
        }

        // stop typing
        this.dispatch({
          type: "REMOVE_CONVERSATION_TYPING",
          payload: {
            id: message.conversationId,
            person: message.person
          }
        });

        // play sound
        if (message.person.id !== this.context.personId) {
          play("notification");
        }

        // update title
        if (message.person.id !== this.context.personId) {
          prependTitleUntilFocused(
            `${i18n.t("newMessage", {
              name: message.person.name
            })}`
          );
        }
        break;
      case "ADD_CONVERSATION_TYPING":
      case "REMOVE_CONVERSATION_TYPING":
        const typing = payload as IOnEventConversationTyping;
        const conversationTypingFragment = gql`
          fragment conversationTypingFragment on Conversation {
            typing
          }
        `;

        if (typing.person.id === this.context.personId) {
          return;
        }

        try {
          const conversation = client.readFragment<{
            typing: IPersonFragment[];
            __typename?: string;
          }>({
            id: `Conversation:${typing.id}`,
            fragment: conversationTypingFragment
          });

          if (!conversation) {
            return;
          }

          const timeoutKey = `${typing.id}:${typing.person.id}`;
          clearTimeout(this.timeoutTyping[timeoutKey]);

          if (type === "REMOVE_CONVERSATION_TYPING") {
            client.writeFragment({
              id: `Conversation:${typing.id}`,
              fragment: conversationTypingFragment,
              data: {
                ...conversation,
                typing: conversation.typing.filter(
                  t => t.id !== typing.person.id
                )
              }
            });
          }

          if (type === "ADD_CONVERSATION_TYPING") {
            if (
              conversation.typing.find(p => p.id === typing.person.id) ===
              undefined
            ) {
              client.writeFragment({
                id: `Conversation:${typing.id}`,
                fragment: conversationTypingFragment,
                data: {
                  ...conversation,
                  typing: [...conversation.typing, typing.person]
                }
              });
            }

            this.timeoutTyping[timeoutKey] = setTimeout(() => {
              this.dispatch({
                type: "REMOVE_CONVERSATION_TYPING",
                payload
              });
            }, TYPING_INTERVAL + 500);
          }
        } catch (error) {
          // ignore for now
        }
        break;
    }
  };

  render() {
    return (
      <SubscriptionManagerContext.Provider value={this.dispatch}>
        {this.props.children}
      </SubscriptionManagerContext.Provider>
    );
  }
}
