import gql from "graphql-tag";
import { PersonFragment, IPersonFragment } from "./person";
import { MessageFragment, IMessageFragment } from "./message";

export interface IConversationFragment {
  id: string;
  createdAt: string;
  updatedAt: string;
  participants: Array<{
    person: IPersonFragment;
  }>;
  state: "o" | "c";
  person: IPersonFragment;
  lastMessage: IMessageFragment;
  assign: {
    to: IPersonFragment;
    __typename?: string;
  };
  typing: [IPersonFragment];
  __typename?: string;
}

export const ConversationFragment = gql`
  fragment ConversationFragment on Conversation {
    id
    createdAt
    updatedAt
    participants {
      person {
        ...PersonFragment
      }
    }
    state
    person {
      ...PersonFragment
    }
    lastMessage {
      ...MessageFragment
    }
    assign {
      to {
        ...PersonFragment
      }
    }
    typing @client
  }
  ${MessageFragment}
`;
