import React, { Component } from "react";
import ApolloClient from "apollo-client";
import {
  IGetAccessTokenData,
  IGetAccessTokenVariables,
  GET_ACCESS_TOKEN
} from "../api/mutations/GetAccessToken";
import { STORAGE } from "../constants";
import * as jwt from "../utils/jwt";
import { REFRESH_TOKEN } from "../api/mutations/RefreshToken";

export interface ICustomerContext {
  accountId: string;
  personId: string;
  channelInstallationId: string;
  company: string;
}

export const CustomerContext = React.createContext<ICustomerContext>({
  accountId: "",
  personId: "",
  company: "",
  channelInstallationId: ""
});

interface IProps {
  client: ApolloClient<{}>;
}
interface IState extends ICustomerContext {}

export default class CustomerManager extends Component<IProps, IState> {
  private refreshTimer: any;

  constructor(props: IProps) {
    super(props);

    this.state = {
      accountId: "",
      personId: "",
      company: "",
      channelInstallationId: ""
    };
  }

  refresh = (token: string) => {
    const { client } = this.props;

    const FIVE_MINUTES = 60 * 5;
    const expiresIn = jwt.willExpireIn(token);
    if (expiresIn <= FIVE_MINUTES) {
      client
        .mutate({
          mutation: REFRESH_TOKEN,
          variables: {
            token
          }
        })
        .then(
          ({ data }) => {
            if (data && data.refreshToken) {
              localStorage.setItem(STORAGE.TOKEN, data.refreshToken.token);
            }
          },
          () => {
            if (expiresIn < 0) {
              this.setState(() => ({
                accountId: "",
                personId: "",
                company: "",
                channelInstallationId: ""
              }));
            }
          }
        );
    }
  };

  refreshLater = () => {
    const ONE_MINUTE = 1000 * 60 * 1;
    const token = localStorage.getItem(STORAGE.TOKEN);
    if (token) {
      this.refresh(token);
      this.refreshTimer = setTimeout(() => {
        this.refreshLater();
      }, ONE_MINUTE);
    }
  };

  componentDidMount() {
    const { client } = this.props;
    const chatSettings = (window as any).chatSettings as {
      company: string;
      accountId: string;
      customer: {
        name?: string;
        email?: string;
        customerId?: string;
      };
      channelInstallationId: string;
    };

    client
      .mutate<IGetAccessTokenData, IGetAccessTokenVariables>({
        mutation: GET_ACCESS_TOKEN,
        variables: {
          accountId: chatSettings.accountId,
          ...chatSettings.customer
        }
      })
      .then(
        ({ data }) => {
          localStorage.setItem(STORAGE.TOKEN, data.getAccessToken.accessToken);
          this.setState(
            {
              accountId: chatSettings.accountId,
              personId: data.getAccessToken.customer.id,
              company: chatSettings.company,
              channelInstallationId: chatSettings.channelInstallationId
            },
            () => {
              this.refreshLater();
            }
          );
        },
        error => {
          console.error(error);
        }
      );
  }

  public componentWillUnmount() {
    if (this.refreshTimer) {
      clearTimeout(this.refreshTimer);
    }
  }

  render() {
    const { accountId, personId } = this.state;

    if (!accountId || !personId) {
      return <React.Fragment />;
    }

    return (
      <CustomerContext.Provider value={this.state}>
        {this.props.children}
      </CustomerContext.Provider>
    );
  }
}
