import React from "react";
import {
  IMessageFragment,
  IMessagePartQuickReplyResponse
} from "../../../api/fragments/message";
import { format } from "../../../utils/date";
import styled from "../../../styled-components";

interface IProps {
  message: IMessageFragment;
  response: IMessagePartQuickReplyResponse;
  className?: string;
}

const QuickResponse = ({ message, response, className }: IProps) => {
  return (
    <div className={className} title={format(message.createdAt, "Pp")}>
      {response.title}
    </div>
  );
};

export default styled(QuickResponse)`
  padding: 0.75em;
  max-width: 75%;
  margin: 0.075em 0;
  word-break: break-word;

  ${props => `
    background: ${props.theme.primaryColor};
    color: #fff;
    align-self: flex-end;
    border-radius: ${props.theme.messageBorderRadius};
  `}
`;
