import React from "react";
import buttonLogo from "../assets/img/bot.png";
import styled from "../styled-components";
import { zoomIn } from "../theme";

interface IProps {
  onClick: () => void;
  opened: boolean;
}

export default ({ onClick, opened }: IProps) => {
  return (
    <FabStyled onClick={onClick}>
      <div className="button">
        {opened ? (
          <i className="fa fa-times" />
        ) : (
          <img src={buttonLogo} alt={"logo"} />
        )}
      </div>
    </FabStyled>
  );
};

const FabStyled = styled.div`
  display: flex;
  flex-direction: row-reverse;
  transition: all 0.1s ease-out;

  .button {
    margin: 10px 0 0 0;
    background: ${props => props.theme.primaryColor};
    color: #fff;
    width: 60px;
    height: 60px;
    border-radius: 30px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    font-size: 2em;
    cursor: pointer;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.14), 0 4px 8px rgba(0, 0, 0, 0.28);
    animation: ${zoomIn} 0.2s cubic-bezier(0.42, 0, 0.58, 1);
  }
`;
