import gql from "graphql-tag";
import { IMessageFragment, MessageFragment } from "../fragments/message";
import { IPersonFragment } from "../fragments/person";

export interface IOnEventVariables {}

export interface IOnEventData {
  onEvent: {
    name: "messageReceived" | "conversationTyping";
    data: IOnEventMessageReceived | IOnEventConversationTyping;
  };
}

export interface IOnEventResult {
  data: IOnEventData;
}

export const ON_EVENT = gql`
  subscription {
    onEvent {
      name
      data {
        ... on MessageReceived {
          message {
            ...MessageFragment
          }
          conversation {
            id
            assign {
              to {
                ...PersonFragment
              }
            }
          }
        }
        ... on ConversationTyping {
          id
          person {
            ...PersonFragment
          }
        }
      }
    }
  }
  ${MessageFragment}
`;

// conversationTyping
export interface IOnEventConversationTyping {
  id: string;
  person: IPersonFragment;
}

// messageReceived
export interface IOnEventMessageReceived {
  message: IMessageFragment;
  conversation: {
    id: string;
    assign: {
      to: IPersonFragment;
      __typename?: string;
    };
  };
}
