import gql from "graphql-tag";
import { IPersonFragment, PersonFragment } from "./person";

export type InputType = "TEXT" | "NUMBER" | "EMAIL" | "REGEXP";

export interface IMessageFragment {
  id: string;
  text: string;
  conversationId: string;
  person: IPersonFragment;
  createdAt: string;
  readBy: IPersonFragment[];
  parts: IMessagePart[];
  type: string;
  __typename?: string;
}

export interface IMessagePart {
  type: string;
  content:
    | IMessagePartText
    | IMessagePartAttachment
    | IMessagePartQuickReply
    | IMessagePartQuickReplyResponse
    | IMessagePartInput
    | IMessagePartInputResponse;
  __typename?: string;
}

export interface IMessagePartText {
  text: string;
  __typename?: string;
}

export interface IMessagePartAttachment {
  storageId: string;
  originalName: string;
  location: string;
  contentType: string;
  __typename?: string;
}

export interface IMessagePartQuickReply {
  options: IMessagePartQuickReplyItem[];
  __typename?: string;
}

export interface IMessagePartQuickReplyItem {
  type: string;
  title: string;
  iconUrl: string;
  payload: IQuickReplyText;
  __typename?: string;
}

export interface IMessagePartQuickReplyResponse {
  type: string;
  title: string;
  payload: IQuickReplyText;
  __typename?: string;
}

export interface IQuickReplyText {
  text: string;
  __typename?: string;
}

export interface IMessagePartInput {
  inputType: InputType;
  inputPayload: string;
  name: string;
  description?: string;
  data?: {
    regExp: string;
  };
  response?: IMessagePartInputResponse; // @client
  __typename?: string;
}

export interface IMessagePartInputResponse {
  inputType: InputType;
  inputPayload: string;
  value: string;
  __typename?: string;
}

export const MessageFragment = gql`
  fragment MessageFragment on Message {
    id
    parts {
      type
      content {
        ... on MessagePartText {
          text
        }
        ... on MessagePartAttachment {
          storageId
          originalName
          location
          contentType: type
        }
        ... on MessagePartQuickReply {
          options {
            type
            title
            iconUrl
            payload {
              ... on QuickReplyText {
                text
              }
            }
          }
        }
        ... on MessagePartQuickReplyResponse {
          type
          title
          payload {
            ... on QuickReplyResponseText {
              text
            }
          }
        }
        ... on MessagePartInput {
          inputType: type
          inputPayload: payload
          name
          description
          data {
            ... on MessagePartInputRegExpData {
              regExp
            }
          }
        }
        ... on MessagePartInputResponse {
          inputType: type
          inputPayload: payload
          value
        }
      }
    }
    text
    type
    conversationId
    person {
      ...PersonFragment
    }
    readBy {
      ...PersonFragment
    }
    createdAt
  }
  ${PersonFragment}
`;
