import gql from "graphql-tag";

export interface IPersonFragment {
  id: string;
  name: string;
  email: string;
  type: "CUSTOMER" | "VISITOR" | "USER" | "TEAM";
  __typename?: string;
}

export const PersonFragment = gql`
  fragment PersonFragment on Person {
    id
    name
    email
    type
  }
`;
