import gql from "graphql-tag";
import {
  ConversationFragment,
  IConversationFragment
} from "../fragments/conversation";
import { Query } from "react-apollo";
import { IPersonFragment } from "../fragments/person";

export interface IConversationsVariables {
  personId: string;
  cursor?: string;
}

export interface IConversationsData {
  conversations: {
    total: number;
    items: IConversationFragment[];
  };
  me: IPersonFragment;
}

export const CONVERSATIONS = gql`
  query($personId: ID!, $cursor: String) {
    conversations(
      personId: $personId
      personType: CUSTOMER
      baseCursorTime: $cursor
    ) {
      total
      items {
        ...ConversationFragment
      }
    }
    me {
      ...PersonFragment
    }
  }
  ${ConversationFragment}
`;

export class ConversationsQuery extends Query<
  IConversationsData,
  IConversationsVariables
> {}
