import { emojiIndex, Emoji, EmojiProps } from "emoji-mart";
import { EMOJI_SET } from "../constants";
import emojiRegex from "emoji-regex";
import { BaseEmoji } from "emoji-mart/dist-es/utils/emoji-index/nimble-emoji-index";

const emojiIndexByNative: { [native: string]: BaseEmoji } = Object.values(
  emojiIndex.emojis
).reduce((aggr, curr) => {
  if ("native" in curr) {
    return {
      ...aggr,
      [curr.native]: curr
    };
  }

  return aggr;
}, {});

const emojiSupported = (() => {
  // http://crocodillon.com/blog/parsing-emoji-unicode-in-javascript

  const node = document.createElement("canvas");

  if (!!node) {
    if (!node.getContext || !node.getContext("2d")) {
      return false;
    }
    const ctx = node.getContext("2d");
    if (!ctx || typeof ctx.fillText !== "function") {
      return false;
    }
    ctx.textBaseline = "top";
    ctx.font = "32px Arial";
    ctx.fillText("\ud83d\ude03", 0, 0);
    return ctx.getImageData(16, 16, 1, 1).data[0] !== 0;
  }
})();

export const hasEmojiSupport = () => emojiSupported || false;

export const getEmojiDataByNativeString = (nativeString: string) => {
  // https://github.com/missive/emoji-mart/issues/16

  const skinTones = ["", "", "", "", "", ""];

  let skin;
  let baseNativeString = nativeString;

  skinTones.forEach(skinTone => {
    baseNativeString = baseNativeString.replace(skinTone, "");
    if (nativeString.indexOf(skinTone) > 0) {
      skin = skinTones.indexOf(skinTone) + 1;
    }
  });

  const emojiData = emojiIndexByNative[nativeString];

  if (emojiData && skin && "skin" in emojiData) {
    emojiData.skin = skin;
  }

  return emojiData;
};

const getEmojiHTML = (props: EmojiProps): string => {
  // TODO: modified emoji-mart to support img
  let html = "" + Emoji({ ...props, children: null, html: true });
  html = html.replace(
    "<span",
    `<img src="data:image/gif;base64,"`
  );
  html = html.replace("</span>", "</img>");
  return html;
};

type ParseEmojisToHTMLOutput = {
  html: string;
  table: {
    [html: string]: string;
  };
};

export const parseEmojisToHTML = (text: string) => {
  const regex = emojiRegex();

  const output: ParseEmojisToHTMLOutput = {
    html: text,
    table: {}
  };

  let match: RegExpExecArray | null;
  while ((match = regex.exec(output.html))) {
    const emoji = match[0];
    const emojiData = getEmojiDataByNativeString(emoji);
    if (emojiData) {
      const emojiHTML = getEmojiHTML({
        set: EMOJI_SET,
        emoji: emojiData,
        size: 24
      });

      output.table[emojiHTML] = emoji;
      output.html = output.html.replace(emoji, emojiHTML);
    }
  }

  return output;
};
