import React, { Component } from "react";
import Fab from "./components/Fab";
import SceneManager from "./containers/SceneManager";
import SubscriptionManager from "./containers/SubscriptionManager";
import { ApolloConsumer } from "react-apollo";
import styled from "./styled-components";
import { zoomIn } from "./theme";
import CustomerManager from "./containers/CustomerManager";
import { emojiMartStyles } from "./emoji-mart";

interface IProps {}

interface IState {
  opened: boolean;
}

class App extends Component<IProps, IState> {
  constructor(props: IProps) {
    super(props);

    this.state = {
      opened: true
    };
  }

  toggle = () => {
    this.setState(({ opened }) => ({
      opened: !opened
    }));
  };

  render() {
    const { opened } = this.state;
    return (
      <ApolloConsumer>
        {client => (
          <CustomerManager client={client}>
            <SubscriptionManager client={client}>
              {opened && (
                <AppStyled>
                  <SceneManager />
                </AppStyled>
              )}
              <Fab opened={opened} onClick={this.toggle} />
            </SubscriptionManager>
          </CustomerManager>
        )}
      </ApolloConsumer>
    );
  }
}

const AppStyled = styled.div`
  display: flex;
  flex-direction: column;
  height: 530px;
  width: 353px;
  border-radius: ${({ theme }) => theme.wrapperBorderRadius};
  box-shadow: 1px 1px 100px 2px rgba(0, 0, 0, 0.22);
  flex: 1;
  background: #fff;
  font-family: ${({ theme }) => theme.fontFamily};
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  animation: ${zoomIn} 0.2s cubic-bezier(0.42, 0, 0.58, 1);

  * {
    box-sizing: border-box;
  }

  ${emojiMartStyles}
`;

export default App;
