import React from "react";
import Header from "../../components/Header";
import Content from "./components/Content";
import Footer from "./components/Footer";
import I18n from "../../components/I18n";
import { IConversationFragment } from "../../api/fragments/conversation";
import {
  MESSAGES,
  MessagesQuery,
  IMessagesData,
  IMessagesVariables
} from "../../api/queries/MessagesQuery";
import { SceneManagerDispatch } from "../../containers/SceneManager";
import {
  MarkMessageAsReadMutation,
  MARK_MESSAGE_AS_READ
} from "../../api/mutations/MarkMessageAsReadMutation";
import Loading from "../../components/Loading";
import styled from "../../styled-components";
import { play } from "../../utils/audio";
import { TranslationFunction } from "i18next";
import { IMessageFragment } from "../../api/fragments/message";
import { firstName } from "../../utils/name";
import {
  CustomerContext,
  ICustomerContext
} from "../../containers/CustomerManager";
import { getConversationTitle } from "../Conversations/components/ConversationItem";

interface IProps {
  dispatch: SceneManagerDispatch;
  conversationId: string;
}

const Chat = (props: IProps) => {
  const { conversationId, dispatch } = props;

  const onCloseConversation = () => {
    dispatch({
      type: "CLOSE"
    });
  };

  const onMessageSent = (scrollDown: () => void) => () => {
    scrollDown();
    play("sent");
  };

  return (
    <I18n>
      {t => (
        <CustomerContext.Consumer>
          {context => (
            <MessagesQuery
              query={MESSAGES}
              variables={{ conversationId }}
              notifyOnNetworkStatusChange
            >
              {({ data, loading, error, fetchMore }) => {
                if (error) {
                  return `Error! ${error.message}`;
                }

                if (loading && (!data || !data.messages)) {
                  return <Loading />;
                }

                if (!data) {
                  return "Error! data is undefined";
                }

                const total = data.messages ? data.messages.total : 0;
                const messages = data.messages ? data.messages.items : [];
                const conversation = data.conversation;
                const customer = data.me;

                return (
                  <MarkMessageAsReadMutation mutation={MARK_MESSAGE_AS_READ}>
                    {markMessageAsReadMutate => (
                      <Content
                        onLoadMore={(cursor: string) => {
                          fetchMore({
                            variables: { conversationId, cursor },
                            updateQuery: (previous, { fetchMoreResult }) => {
                              if (!fetchMoreResult) {
                                return previous;
                              }

                              return {
                                ...fetchMoreResult,
                                messages: {
                                  ...fetchMoreResult.messages,
                                  items: [
                                    ...previous.messages.items,
                                    ...fetchMoreResult.messages.items
                                  ]
                                }
                              };
                            }
                          });
                        }}
                        total={total}
                        loading={loading}
                        markAsRead={markMessageAsReadMutate}
                        conversation={conversation}
                        typing={conversation.typing.length > 0}
                        messages={messages}
                      >
                        {({ render: renderContent, scrollDown }) => (
                          <ChatStyled>
                            <Header onCloseConversation={onCloseConversation}>
                              {getConversationTitle(conversation, context, t)}
                            </Header>
                            {renderContent()}
                            <Footer
                              customer={customer}
                              conversation={conversation}
                              onMessageSent={onMessageSent(scrollDown)}
                            />
                          </ChatStyled>
                        )}
                      </Content>
                    )}
                  </MarkMessageAsReadMutation>
                );
              }}
            </MessagesQuery>
          )}
        </CustomerContext.Consumer>
      )}
    </I18n>
  );
};

const ChatStyled = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
  min-height: 0px;
`;

export default Chat;
