import { PersonFragment, IPersonFragment } from "../fragments/person";
import gql from "graphql-tag";
import { Mutation, MutationFn } from "react-apollo";

export interface IMarkMessageAsReadData {
  markMessageAsRead: {
    id: string;
    readBy: IPersonFragment[];
  };
}

export interface IMarkMessageAsReadVariables {
  conversationId: string;
  messageIds: string[];
}

export const MARK_MESSAGE_AS_READ = gql`
  mutation($conversationId: ID!, $messageIds: [ID]!) {
    markMessageAsRead(
      conversationId: $conversationId
      messageIds: $messageIds
    ) {
      id
      readBy {
        ...PersonFragment
      }
    }
  }
  ${PersonFragment}
`;

export class MarkMessageAsReadMutation extends Mutation<
  IMarkMessageAsReadData,
  IMarkMessageAsReadVariables
> {}

export type MarkMessageAsReadMutationFn = MutationFn<
  IMarkMessageAsReadData,
  IMarkMessageAsReadVariables
>;
