import i18n from "../i18n";
import { default as _formatDistance } from "date-fns/formatDistance";
import { default as _formatRelative } from "date-fns/formatRelative";
import { default as _format } from "date-fns/format";
import pt_BR from "date-fns/locale/pt-BR";

const locales: { [language: string]: Locale } = {
  pt_BR
};

type Options = {
  weekStartsOn?: 0 | 1 | 2 | 3 | 4 | 5 | 6;
  firstWeekContainsDate?: 1 | 2 | 3 | 4 | 5 | 6 | 7;
  additionalDigits?: 0 | 1 | 2;
  includeSeconds?: boolean;
  addSuffix?: boolean;
  unit?: "second" | "minute" | "hour" | "day" | "month" | "year";
  roundingMethod?: "floor" | "ceil" | "round";
  awareOfUnicodeTokens?: boolean;
};

export const formatDistance = (
  date: Date | string | number,
  baseDate: Date | string | number,
  options?: Options
) =>
  _formatDistance(date, baseDate, {
    ...options,
    locale: locales[i18n.language]
  });

export const formatRelative = (
  date: Date | string | number,
  baseDate: Date | string | number,
  options?: Options
) =>
  _formatRelative(date, baseDate, {
    ...options,
    locale: locales[i18n.language]
  });

export const format = (
  date: Date | string | number,
  format: string,
  options?: Options
) => _format(date, format, { ...options, locale: locales[i18n.language] });
