import { TITLE_ON_MESSAGE_INTERVAL } from "../constants";

const options: {
  originalTitle: string;
  workingTitle?: string;
  activeTimeout?: NodeJS.Timeout;
} = {
  originalTitle: document.title
};

export const prependTitleUntilFocused = (title: string) => {
  if (options.activeTimeout) {
    clearTimeout(options.activeTimeout);
  }
  options.workingTitle = `${title} - ${options.originalTitle}`;
  setTitle(options.workingTitle);

  options.activeTimeout = setTimeout(restoreTitle, TITLE_ON_MESSAGE_INTERVAL);
};

const restoreTitle = () => {
  if (document.hasFocus()) {
    setTitle(options.originalTitle);
  } else {
    options.activeTimeout = setTimeout(restoreTitle, 1000);
  }
};

const setTitle = (title: string) => {
  document.title = title;
};
