import React from "react";
import {
  IMessageFragment,
  IMessagePartQuickReply,
  IMessagePartQuickReplyItem
} from "../../../api/fragments/message";
import styled from "../../../styled-components";
import { IConversationFragment } from "../../../api/fragments/conversation";
import I18n from "../../../components/I18n";
import { MESSAGE_PART_TYPE, MESSAGE_TYPE } from "../../../constants";
import { SubscriptionManagerContext } from "../../../containers/SubscriptionManager";
import {
  SendMessageMutation,
  SEND_MESSAGE,
  SendMessageMutationFn
} from "../../../api/mutations/SendMessageMutation";

interface IProps {
  message: IMessageFragment;
  quickReply: IMessagePartQuickReply;
  className?: string;
  conversation?: IConversationFragment;
}

export class QuickReply extends React.Component<IProps> {
  static contextType = SubscriptionManagerContext;
  context!: React.ContextType<typeof SubscriptionManagerContext>;

  onOptionSelected = (
    sendMessage: SendMessageMutationFn,
    option: IMessagePartQuickReplyItem
  ) => () => {
    const { message, conversation } = this.props;
    if (!conversation || conversation.lastMessage.id !== message.id) return;

    sendMessage({
      variables: {
        conversationId: message.conversationId,
        text: option.title,
        parts: [
          {
            type: "QUICK_REPLY_RESPONSE",
            content: {
              response: {
                type: MESSAGE_PART_TYPE.TEXT,
                text: option.payload.text,
                title: option.title
              }
            }
          }
        ]
      },
      optimisticResponse: {
        sendMessage: {
          id: "",
          text: option.title,
          conversationId: conversation.id,
          createdAt: new Date().toISOString(),
          person: {
            ...conversation.person
          },
          readBy: [],
          parts: [
            {
              type: MESSAGE_PART_TYPE.QUICK_REPLY_RESPONSE,
              content: {
                title: option.title,
                type: "text",
                payload: option.payload,
                __typename: "MessagePartQuickReplyResponse"
              },
              __typename: "MessagePart"
            }
          ],
          type: MESSAGE_TYPE.MESSAGE,
          __typename: "Message"
        }
      },
      update: (_, { data }) => {
        if (!data) {
          return;
        }
        this.context({
          type: "MESSAGE_RECEIVED",
          payload: {
            message: data.sendMessage,
            conversation
          }
        });
      }
    }).then(data => {
      console.log("success", data);
    });
  };

  render() {
    const { className, quickReply } = this.props;

    return (
      <I18n>
        {t => (
          <SendMessageMutation mutation={SEND_MESSAGE}>
            {mutate => {
              return (
                <div className={className}>
                  {quickReply.options.map((opt, i) => (
                    <Option
                      onClick={this.onOptionSelected(mutate, opt)}
                      key={i}
                    >
                      {opt.title}
                    </Option>
                  ))}
                </div>
              );
            }}
          </SendMessageMutation>
        )}
      </I18n>
    );
  }
}

export default styled(QuickReply)`
  margin: 1.2em 0em;
  text-align: center;
`;

const Title = styled.p`
  color: #6c757d;
  font-size: 0.75em;
  margin-bottom: 1.3em;
`;

const Option = styled.span`
  padding: 0.5em 0.75em;
  margin-right: 0.5em;
  margin-bottom: 0.5em;
  border-radius: ${props => props.theme.messageBorderRadius};
  display: inline-block;
  border: 0.09em ${props => props.theme.primaryColor} solid;
  color: ${props => props.theme.primaryColor};
  transition: all 0.1s ease-in;
  :hover {
    background: ${props => props.theme.primaryColor};
    cursor: pointer;
    color: #fff;
  }
`;
