import { Query } from "react-apollo";
import gql from "graphql-tag";
import { MessageFragment, IMessageFragment } from "../fragments/message";
import {
  ConversationFragment,
  IConversationFragment
} from "../fragments/conversation";
import { IPersonFragment } from "../fragments/person";

export interface IMessagesData {
  messages: {
    total: number;
    items: IMessageFragment[];
  };
  conversation: IConversationFragment;
  me: IPersonFragment;
}

export interface IMessagesVariables {
  conversationId: string;
  cursor?: string;
}

export const MESSAGES = gql`
  query messages($conversationId: ID!, $cursor: ID) {
    messages(
      conversationId: $conversationId
      baseMessageId: $cursor
      type: MESSAGE
    ) {
      total
      items {
        ...MessageFragment
      }
    }
    conversation(id: $conversationId) {
      ...ConversationFragment
    }
    me {
      ...PersonFragment
    }
  }
  ${MessageFragment}
  ${ConversationFragment}
`;

export class MessagesQuery extends Query<IMessagesData, IMessagesVariables> {}
