import * as React from "react";
import { NamespacesConsumer, Namespace } from "react-i18next";
import i18next from "i18next";

interface IProps {
  ns?: Namespace;
  initialI18nStore?: {};
  initialLanguage?: string;
  children(
    t: i18next.TranslationFunction,
    options: {
      i18n: i18next.i18n;
      lng: string;
      ready: boolean;
    }
  ): React.ReactNode;
}

export default (props: IProps) => {
  const ns = props.ns || "";
  return <NamespacesConsumer ns={ns} {...props} />;
};
