import { CDN_URI } from "../constants";

const SOUNDS = {
  notification: `${CDN_URI}/sounds/unconvinced.mp3`,
  sent: `${CDN_URI}/sounds/pull-out.mp3`
};

export const play = (soundId: "notification" | "sent") => {
  if ("Audio" in window) {
    const sound = new Audio(SOUNDS[soundId]);
    sound.play();
  }
};
