import gql from "graphql-tag";
import {
  MessageFragment,
  IMessageFragment,
  InputType
} from "../fragments/message";
import { Mutation, MutationFn } from "react-apollo";

export interface ISendMessageData {
  sendMessage: IMessageFragment;
}

export interface ISendMessageVariables {
  conversationId?: string;
  channelInstallationId?: string;
  text: string;
  parts: ISendMessagePart[];
}

export interface ISendMessagePart {
  type:
    | "TEXT"
    | "ATTACHMENT"
    | "QUICK_REPLY"
    | "QUICK_REPLY_RESPONSE"
    | "INPUT"
    | "INPUT_RESPONSE";
  content:
    | ISendMessagePartContentText
    | ISendMessagePartContentAttachment
    | ISendMessagePartContentQuickReply
    | ISendMessagePartContentQuickReplyResponse
    | ISendMessagePartContentInputResponse;
}
export interface ISendMessagePartContentText {
  text: string;
}
export interface ISendMessagePartContentAttachment {
  attachment: {
    storageId: string;
  };
}
export interface ISendMessagePartContentQuickReply {
  options: ISendMessagePartContentQuickReplyOption[];
}
export interface ISendMessagePartContentQuickReplyOption {
  type: string;
  title: string;
  text?: string;
  iconUrl: string;
}
export interface ISendMessagePartContentQuickReplyResponse {
  response: ISendMessagePartContentResponse;
}
export interface ISendMessagePartContentResponse {
  type: string;
  title: string;
  text?: string;
  lat?: number;
  lng?: number;
}

export interface ISendMessagePartContentInputResponse {
  inputResponse: {
    type: InputType;
    value: string;
    payload: string;
  };
}

export const SEND_MESSAGE = gql`
  mutation sendMessage(
    $text: String!
    $conversationId: ID
    $channelInstallationId: ID
    $parts: [MessagePartInputObject]!
  ) {
    sendMessage(
      conversationId: $conversationId
      parts: $parts
      text: $text
      channelInstallationId: $channelInstallationId
    ) {
      ...MessageFragment
    }
  }
  ${MessageFragment}
`;

export class SendMessageMutation extends Mutation<
  ISendMessageData,
  ISendMessageVariables
> {}

export type SendMessageMutationFn = MutationFn<
  ISendMessageData,
  ISendMessageVariables
>;
