import gql from "graphql-tag";
import { Mutation, MutationFn } from "react-apollo";

export interface IGetAccessTokenData {
  getAccessToken: {
    accessToken: string;
    customer: { id: string };
  };
}

export interface IGetAccessTokenVariables {
  accountId: string;
  email?: string;
  customerId?: string;
  name?: string;
}

export const GET_ACCESS_TOKEN = gql`
  mutation(
    $accountId: String!
    $email: String
    $customerId: String
    $name: String
  ) {
    getAccessToken(
      accountId: $accountId
      email: $email
      customerId: $customerId
      name: $name
    ) {
      accessToken
      customer {
        id
      }
    }
  }
`;

export class GetAccessTokenMutation extends Mutation<
  IGetAccessTokenData,
  IGetAccessTokenVariables
> {}

export type GetAccessTokenMutationFn = MutationFn<
  IGetAccessTokenData,
  IGetAccessTokenVariables
>;
