import gql from "graphql-tag";
import { Mutation, MutationFn } from "react-apollo";

export interface IStartTypingConversationData {
  id: string;
}

export interface IStartTypingConversationVariables {
  conversationId: string;
}

export const START_TYPING_CONVERSATION = gql`
  mutation($conversationId: ID!) {
    startTypingConversation(conversationId: $conversationId) {
      id
    }
  }
`;

export class StartTypingConversationMutation extends Mutation<
  IStartTypingConversationData,
  IStartTypingConversationVariables
> {}

export type StartTypingConversationMutationFn = MutationFn<
  IStartTypingConversationData,
  IStartTypingConversationVariables
>;
