import React from "react";
import I18n from "../../../components/I18n";
import { CONVERSATIONS } from "../../../api/queries/ConversationsQuery";
import {
  SendMessageMutationFn,
  SendMessageMutation,
  SEND_MESSAGE
} from "../../../api/mutations/SendMessageMutation";
import styled from "../../../styled-components";
import { CustomerContext } from "../../../containers/CustomerManager";

interface IProps {
  onNewConversation: (conversationId: string) => void;
  channelInstallationId: string;
}

interface IState {
  message: string;
}

export default class NewConversation extends React.Component<IProps, IState> {
  static contextType = CustomerContext;
  context!: React.ContextType<typeof CustomerContext>;

  constructor(props: IProps) {
    super(props);

    this.state = {
      message: ""
    };
  }

  private onMessageChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const { value } = event.target;
    this.setState(() => ({
      message: value
    }));
  };

  private onNewConversation = (sendMessage: SendMessageMutationFn) => (
    e: React.FormEvent
  ) => {
    e.preventDefault();
    const { message } = this.state;
    const { channelInstallationId } = this.props;

    const data = message.trim();

    if (data === "") {
      return;
    }

    sendMessage({
      variables: {
        channelInstallationId,
        text: data,
        parts: [
          {
            type: "TEXT",
            content: { text: data }
          }
        ]
      }
    }).then(
      result => {
        if (!result || !result.data) {
          console.error("sendMessage result undefined", result);
          return;
        }
        this.props.onNewConversation(result.data.sendMessage.conversationId);
      },
      error => console.log(error)
    );
  };

  render() {
    const { message } = this.state;
    return (
      <I18n>
        {t => (
          <Wrapper>
            <h3>{t("new conversation")}</h3>
            <SendMessageMutation
              mutation={SEND_MESSAGE}
              refetchQueries={[
                {
                  query: CONVERSATIONS,
                  variables: { personId: this.context.personId }
                }
              ]}
            >
              {sendMessage => (
                <Form onSubmit={this.onNewConversation(sendMessage)}>
                  <Input
                    className={`message-input`}
                    value={message}
                    onChange={this.onMessageChange}
                    placeholder={t("chat.input.placeholder")}
                  />
                  <Button onClick={this.onNewConversation(sendMessage)}>
                    <span className="fa fa-paper-plane" />
                  </Button>
                </Form>
              )}
            </SendMessageMutation>
          </Wrapper>
        )}
      </I18n>
    );
  }
}

const Input = styled.input`
  flex-grow: 1;
  padding: 0.3em;
  border: none;
  font-family: ${({ theme }) => theme.fontFamily};
  font-size: 1em;
  resize: none;

  &:focus {
    outline: none;
  }
`;

const Form = styled.form`
  display: flex;
  flex-shrink: 0;
  align-items: center;
  border: 1px solid #ccc;
  border-radius: 0.5em;
  padding: 0.4em 0.2em;
`;

const Wrapper = styled.div`
  margin: 0 0.75em;

  h3 {
    margin: 1em 0;
  }
`;

const Button = styled.div`
  cursor: pointer;
  padding-right: 0.3em;
  font-size: 1.25em;
  color: ${props => props.theme.primaryColor};
`;
