import React from "react";
import Chat from "../scenes/Chat";
import Conversations from "../scenes/Conversations";
import { STORAGE } from "../constants";

interface IProps {}

interface IState {
  conversationId?: string;
}

export type SceneManagerAction = {
  type: "CLOSE" | "OPEN";
  payload?: string;
};

export type SceneManagerDispatch = (action: SceneManagerAction) => void;

export default class SceneManager extends React.Component<IProps, IState> {
  constructor(props: IProps) {
    super(props);

    this.state = {};
  }

  dispatch = ({ type, payload }: SceneManagerAction) => {
    switch (type) {
      case "CLOSE":
        this.setState(
          () => ({
            conversationId: undefined
          }),
          this.afterDispatch
        );
      case "OPEN":
        this.setState(
          () => ({
            conversationId: payload
          }),
          this.afterDispatch
        );
    }
  };

  afterDispatch = () => {
    localStorage.setItem(STORAGE.SCENE, JSON.stringify(this.state));
  };

  componentDidMount() {
    const stateJSON = localStorage.getItem(STORAGE.SCENE);
    if (!!stateJSON) {
      const state = JSON.parse(stateJSON);
      if ("conversationId" in state) {
        this.dispatch({
          type: "OPEN",
          payload: state.conversationId
        });
      }
    }
  }

  render() {
    const { conversationId } = this.state;

    if (!!conversationId) {
      return <Chat conversationId={conversationId} dispatch={this.dispatch} />;
    }

    return <Conversations dispatch={this.dispatch} />;
  }
}
