import React from "react";
import I18n from "../../components/I18n";
import Header from "../../components/Header";
import { ConversationItem } from "./components/ConversationItem";
import NewConversation from "./components/NewConversation";
import {
  ConversationsQuery,
  CONVERSATIONS,
  IConversationsData
} from "../../api/queries/ConversationsQuery";
import { IConversationFragment } from "../../api/fragments/conversation";
import { SceneManagerDispatch } from "../../containers/SceneManager";
import { createSelector } from "reselect";
import Loading from "../../components/Loading";
import ConversationsSection from "./components/ConversationsSection";
import ConversationsList from "./components/ConversationsList";
import { play } from "../../utils/audio";
import { CustomerContext } from "../../containers/CustomerManager";

interface IProps {
  dispatch: SceneManagerDispatch;
}

export default (props: IProps) => {
  const { dispatch } = props;

  const onConversationSelected = (conversationId: string) => {
    dispatch({
      type: "OPEN",
      payload: conversationId
    });
  };

  return (
    <I18n>
      {t => (
        <CustomerContext.Consumer>
          {({ personId, channelInstallationId }) => (
            <ConversationsQuery
              query={CONVERSATIONS}
              variables={{ personId }}
              notifyOnNetworkStatusChange
            >
              {({ data, loading, error, fetchMore }) => {
                if (error) {
                  return `Error! ${error.message}`;
                }

                if (loading && (!data || !data.conversations)) {
                  return <Loading />;
                }

                if (!data) {
                  return "Error! data is undefined";
                }

                const total = data.conversations.total;
                const conversations = conversationsByLastMessageSelector(data);

                return (
                  <>
                    <Header>{t("hi", { name: data.me.name })}</Header>
                    <NewConversation
                      channelInstallationId={channelInstallationId}
                      onNewConversation={onConversationSelected}
                    />
                    <ConversationsSection>
                      <h3>{t("previous conversations")}</h3>
                      <ConversationsList
                        onScroll={event => {
                          if (loading) return;

                          if (total > 0) {
                            const target = event.target as HTMLDivElement;

                            if (
                              target.scrollTop + target.offsetHeight >=
                              target.scrollHeight - 50
                            ) {
                              fetchMore({
                                variables: {
                                  personId,
                                  cursor:
                                    conversations[conversations.length - 1]
                                      .updatedAt
                                },
                                updateQuery: (
                                  previous,
                                  { fetchMoreResult }
                                ) => {
                                  if (!fetchMoreResult) {
                                    return previous;
                                  }

                                  return {
                                    ...previous,
                                    conversations: {
                                      ...fetchMoreResult.conversations,
                                      items: [
                                        ...previous.conversations.items,
                                        ...fetchMoreResult.conversations.items
                                      ]
                                    }
                                  };
                                }
                              });
                            }
                          }
                        }}
                      >
                        {conversations.map(c => (
                          <ConversationItem
                            key={c.id}
                            onSelect={onConversationSelected}
                            conversation={c}
                          />
                        ))}
                        {loading && <Loading partial />}
                      </ConversationsList>
                    </ConversationsSection>
                  </>
                );
              }}
            </ConversationsQuery>
          )}
        </CustomerContext.Consumer>
      )}
    </I18n>
  );
};

const conversationsSelector = (data: IConversationsData) =>
  data.conversations.items;

const conversationsByLastMessageSelector = createSelector(
  conversationsSelector,
  conversations => {
    return [...conversations].sort((a, b) => {
      return (
        Number(new Date(b.lastMessage.createdAt)) -
        Number(new Date(a.lastMessage.createdAt))
      );
    });
  }
);
